.header{margin:0;padding:1rem;grid-area:header;align-items:start;justify-self:start;display:grid;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(1, 1fr);perspective:100px}.logo{grid-column:1 / span 4;grid-row:1 / span 1;color:#ffff00;text-align:left;font:italic 500 3.5vw "Monoton", sans-serif;text-transform:uppercase;text-shadow:0.2rem 0.2rem 0.15rem black;text-shadow:0.2rem 0.2rem 0.15rem rgba(250,95,105,0.7);letter-spacing:0.35rem;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transform-style:preserve-3d;transform:rotateX(0deg) rotateZ(0deg);color:transparent;color:rgba(255,255,0,0.9);text-shadow:0 0 5px #ff0;position:absolute;z-index:2;font-feature-settings:"c2sc";font-variant:small-caps}.logo>span{color:#d23232}

.Join{display:grid;width:100vw;height:100vh;background:transparent;font:400 2.7rem Montserrat;position:absolute;z-index:2;grid-template-areas:"header  header  header header" ".       .       .       ." ".       .       .       ." ".       .       .       ." ".       .       .       ." ".       .       .       ."}.join-bg{position:absolute;top:0;left:0;right:0;bottom:0;z-index:0;width:100vw;height:100vh;opacity:1;background:linear-gradient(-40deg, rgba(255,0,255,0.2) 0%, rgba(255,0,255,0.2) 15%, rgba(255,0,255,0.4) 35%, rgba(250,15,100,0.2) 60%, rgba(250,15,100,0.1) 70%),linear-gradient(155deg, rgba(255,10,80,0) 0%, rgba(255,10,80,0.05) 50%, rgba(255,10,80,0.1) 60%, rgba(255,10,80,0.3) 80%, rgba(255,10,80,0.5) 95%),linear-gradient(-145deg, rgba(0,255,255,0.1) 10%, rgba(0,255,255,0.9) 65%, rgba(5,150,250,0.9) 88%)}.panorama{width:100vw;height:100vh;margin:0;top:0;left:0;bottom:0;right:0;position:absolute;z-index:1;opacity:0.15;display:flex;justify-content:center;box-sizing:border-box;background-repeat:no-repeat;background-position:center}.panorama__image{position:relative;z-index:1;width:100vw;height:100vh;-webkit-filter:blur(0.5px);filter:blur(0.5px)}.join-button{display:flex;justify-content:center;align-items:center;align-content:center;flex-direction:column;position:absolute;top:220%;left:50%;transform:translate(-50%, -50%);width:6rem;height:6rem;padding:0.4em;border-radius:100%;border:0.14rem solid rgba(255,255,0,0.6);outline:none;background:rgba(0,0,0,0.1);text-align:center;color:#fff;font:500 2.7vw "Jost";-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;transition:all 0.2s ease}.join-button:hover{background:rgba(0,0,0,0.25);cursor:pointer}.join-button:active{color:#ff0;color:rgba(255,255,0,0.9);text-shadow:0 0 5px #ff0}.join-form{display:flex;justify-content:center;align-items:center;align-content:center;flex-direction:column;outline:none;z-index:3;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);transit