.sidebar{width:20%;height:100%;display:flex;flex-direction:column;box-sizing:border-box;font:400 1.1rem "Open Sans", sans-serif;color:#ddd;border-right:0.1rem solid rgba(63,63,86,0.6)}.sidebar__header{height:11%;width:100%;padding-left:1.2rem;display:flex;align-items:center;border-bottom:0.1rem solid rgba(63,63,86,0.6);border-radius:0.1rem;margin-bottom:1rem}.sidebar__list{display:flex;flex-direction:column;padding-left:1.2rem;color:#99a}.sidebar__item{margin:1rem 0;text-shadow:0.5rem 0.5rem 0.75rem rgba(0,0,0,0.8)}.sidebar__value{margin-top:0.4rem;color:#c8c864}

