.Join {
  display: grid;
  width: 100vw;
  height: 100vh;
  background: transparent;
  font: 400 2.7rem Montserrat;
  position: absolute;
  z-index: 2;

  grid-template-areas:
    "header  header  header header"
    ".       .       .       ."
    ".       .       .       ."
    ".       .       .       ."
    ".       .       .       ."
    ".       .       .       .";

  //   overflow: hidden;
}

.join-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 0;
  width: 100vw;
  height: 100vh;
  opacity: 1;
  background: linear-gradient(
      -40deg,
      rgba(255, 0, 255, 0.2) 0%,
      rgba(255, 0, 255, 0.2) 15%,
      rgba(255, 0, 255, 0.4) 35%,
      rgba(250, 15, 100, 0.2) 60%,
      rgba(250, 15, 100, 0.1) 70%
    ),
    linear-gradient(
      155deg,
      rgba(255, 10, 80, 0) 0%,
      rgba(255, 10, 80, 0.05) 50%,
      rgba(255, 10, 80, 0.1) 60%,
      rgba(255, 10, 80, 0.3) 80%,
      rgba(255, 10, 80, 0.5) 95%
    ),
    linear-gradient(
      -145deg,
      rgba(0, 255, 255, 0.1) 10%,
      rgba(0, 255, 255, 0.9) 65%,
      rgba(5, 150, 250, 0.9) 88%
    );
}

.panorama {
  width: 100vw;
  height: 100vh;
  margin: 0;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: 1;
  opacity: 0.15;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-position: center;

  &__image {
    position: relative;

    z-index: 1;
    width: 100vw;
    height: 100vh;
    filter: blur(0.5px);
  }
}

.join-button {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: column;

  position: absolute;
  top: 220%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 6rem;
  height: 6rem;
  padding: 0.4em;

  border-radius: 100%;
  border: 0.14rem solid rgba(255, 255, 0, 0.6);
  outline: none;

  background: rgba(0, 0, 0, 0.1);
  text-align: center;
  color: rgb(255, 255, 255);
  font: 500 2.7vw "Jost";

  user-select: none;
  transition: all 0.2s ease;

  &:hover {
    background: rgba(0, 0, 0, 0.25);
    cursor: pointer;
  }

  &:active {
    color: rgb(255, 255, 0);
    color: rgba(255, 255, 0, 0.9);
    text-shadow: 0 0 5px rgba(255, 255, 0, 1);
  }
}

@import url("https://fonts.googleapis.com/css2?family=Monoton&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Jost:wght@400;500;700&display=swap");

// user form
.join-form {
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: column;

  outline: none;
  z-index: 3;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: background-color 0.45s;

  margin: 0;
  background: red;
  // height: 12vh;
  // width: 40vw;
  // border: 0.14rem solid rgba(255, 255, 0, 0.6);

  height: 0;
  border: none;
  width: 0;

  border-radius: 5rem;

  text-align: center;
  font: 2.1rem Montserrat;
  background: rgba(0, 0, 0, 0.1);

  &:hover {
    background: rgba(0, 0, 0, 0.25);
  }

  &--moved {
    right: -6.5%;
    top: 0%;
    transform: scale(0.7);
    transition: background-color 0.45s;
    border: none;

    text-align: right;
    background: rgba(0, 0, 0, 0.1);
    &:hover {
      background: rgba(0, 0, 0, 0.25);
    }
  }

  &__hint {
    font-size: 1.1rem;
    font-weight: 500;
    color: rgba(255, 255, 255, 1);
    position: absolute;
    top: 110%;

    transition: background-color 0.45s;
    text-shadow: 0.12rem 0.12rem 0.15rem black;
  }
}

.join-form__input {
  width: 100%;
  height: 100%;
  // padding: 1rem;
  box-sizing: border-box;

  outline: none;
  border: none;

  background: transparent;
  color: #f3eada;

  text-align: inherit;
  text-decoration: none;
  font: 400 2.3rem Raleway;
  border-radius: 5rem;

  transition: all 0.32s;

  &:focus {
    color: #00c89b;
    color: rgba(25, 255, 255, 0.9);
  }

  &--moved {
  }
}

.join-form__input + .join-form__hint {
  display: none;
}

.join-form__input:focus + .join-form__hint {
  display: initial;
}

.username-holder {
  position: absolute;
  top: 50%;
  right: 50%;
  transform: translate(50%, -50%);
  font: 400 2.3rem Raleway;

  text-align: right;

  &--moved {
    transform: translate(-50%, -50%);
    right: 0%;
    top: 0;
    transform: scale(0.7);
    color: #00c89b;
    transition: background-color 0.45s;
  }
}
